import React from "react";

export const BlogLikeItem = ({ like }) => {
  const { usuario } = like;
  return (
    <div
      className="input-group col-md-12 "
      style={{
        flexDirection: "row",
        // backgroundColor: "lightblue",
        borderRadius: 5,
        marginBottom: 5,
        alignContent: "flex-start",
        alignItems: "center",
      }}
    >
      <img
        alt={usuario.nombre}
        src={usuario.images}
        style={{
          width: "30px",
          height: "30px",
          objectFit: "contain",
          borderRadius: 50,
          marginTop: 5,
          marginBottom: 5,
          //   marginLeft: 1,
        }}
      />
      <label className="ml-2">{usuario.nombre.substring(0, 20)}</label>
      {/* <div>
      </div> */}
    </div>
  );
};
