import moment from "moment";
import React from "react";

const defaultImg =
  "https://res.cloudinary.com/dyor179ps/image/upload/v1619550708/APPAne/blog/emm4_ab3yha.png";

export const AutorPubli = ({ fechaPubli, autor, aling }) => {
  return (
    <div
      style={{
        backgroundColor: "transparent",
        display: "flex",
        flexDirection: aling,
        justifyContent: "space-between",
      }}
      className="mb-1 my-1"
    >
      <hr />
      <span>Autor: {autor}</span>
      {moment().diff(fechaPubli, "days") === 0 ? (
        <span>Publicado hace poco</span>
      ) : (
        <span> Publicado hace {moment().diff(fechaPubli, "days")} das</span>
      )}
      <hr />
    </div>
  );
};

export const ImgBlogAjus = ({ image, alt }) => {
  return (
    <img
      alt={alt}
      src={image ? image : defaultImg}
      className="imagenBlogajustada"
    />
  );
};

export const ImgBlog = ({ image, alt }) => {
  return (
    <img alt={alt} src={image ? image : defaultImg} className="imagenBlog" />
  );
};
