import React, { useContext } from "react";
import { useHistory } from "react-router-dom";
import { BlogsContext } from "../../context/BlogsContext";
import { exitoFire, viewErrors } from "../../helpers/messagesUI";

export const BlogButtons = ({ blog }) => {
  const { selecBlog, registraLike, editarBlog, registraLiks } = useContext(
    BlogsContext
  );
  const history = useHistory();

  const handleClick = (e) => {
    e.preventDefault();
    selecBlog(blog);
    history.push("/home/blogview");
  };

  const handleLike = async (e) => {
    e.preventDefault();
    const resp = await registraLike(blog.id);
    if (resp.ok) {
      editarBlog(resp.data);
      selecBlog(resp.data);
      registraLiks(resp.likes);
      exitoFire(resp.msg);
    } else {
      viewErrors(resp);
    }
  };
  return (
    <div>
      {blog && (
        <button
          className="btn btn-outline-primary mt-2 mb-2"
          onClick={handleClick}
        >
          <i className="fa fa-eye bigicon"></i> Ver
        </button>
      )}
      <button
        className="btn btn-outline-primary ml-2 mt-2 mb-2"
        onClick={handleClick}
      >
        <i className="fa fa-comment-medical bigicon"></i> Comentar
      </button>
      <button
        className="btn btn-outline-primary ml-2 mt-2 mb-2"
        onClick={handleLike}
      >
        <i className="fa fa-thumbs-up bigicon"></i> {blog.votos}
      </button>
      <button
        className="btn btn-outline-primary ml-2 mt-2 mb-2"
        onClick={handleClick}
      >
        <i className="fa fa-comments bigicon"></i> 0
      </button>
    </div>
  );
};
